.card {
  margin: 20px;
  width: 18rem;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 30px;
  -webkit-box-shadow: 0px 0px 10px silver;
          box-shadow: 0px 0px 10px silver;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.card-title {
  margin-bottom: 15px;
  color: rgb(3, 4, 94);
}

#closeIcon {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  margin-right: 8px;
  margin-top: 8px;
}

.card-body {
  margin-top: 0px !important;
  padding-top: 0px !important;
}

.button {
  color: rgb(231, 41, 35, 0.6);
  border: transparent;
  border-radius: 12px;
  background-color: transparent;
}

.button:hover {
  background-color: rgb(231, 41, 35, 0.1);
}


